
.orderDetails{
    height:80vh;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: white;
   

    >main{
        background-color: rgb(236, 167, 179);
        height: 100%;
        margin: auto;
        padding: 1rem;
        max-width: 900px;
        overflow-x: auto;
             >h1{
            text-align: center;
            margin: 3rem;
            font:100 3rem "Roboto";
            text-transform: capitalize;
            color: white;
        }
        >div{
            margin: 2rem;
            text-align: center;
        }
        >h1{
            font-size: 1.8rem;
            text-transform: uppercase;
            letter-spacing: 5px;
        }
        >p{
            font-weight: 200;
            white-space: nowrap;
            letter-spacing: 2rem;
            margin: 2rem;
         

        
        
       
    }  
 >article{
    h1{
        font-size: 1.8rem;
        text-transform: uppercase;
        letter-spacing: 5px;
        margin: 2rem 0;
    }
    >div{
        background-color: white;
        margin: 1rem;
        display: flex;
        justify-content: space-between;
        padding: 1rem;
        border-radius: 5px;
        >h4{
            font-weight: 200;
            letter-spacing: 2px;
        }
    
    }
 }   
}
}
.btag{
    margin: 0.7rem;
}