.tableClass{
    background-color: pink;
    width: 100%;
    height: 100vh;
    >main{
    max-width: 900px;
    background-color: wheat;
    margin: auto;
    height: 100%;
    padding: 2rem;
    overflow-x: auto;

>table{
    width: 100%;
    text-align: center;
    border-collapse: collapse;

tr{
    background-color: whitesmoke;
    &:last-child{ 
        td:first-child{
        border-radius:0 0 0 10px;
        
    }
    td:last-child{
        border-radius: 0 0  10px 0;
    }
}
}
td{
    padding: 1rem;
    width: 1rem;
}
th{
    background-color: rgb(3, 0, 0);
    color: white;
    padding: 1rem;
    &:first-child {
        border-radius:10px 0 0 0;
        
    }
    &:last-child{
        border-radius: 0 10px  0 0;
        
    }
}
td{
    color:black;
    
   
      
        >img{
            width: 40px;
            height: 40px;
            border-radius: 100%;
        }

    
    >button{
        border: none;
        cursor: pointer;
        margin: 0.25rem;
        color: red;
    }
}
}}
}
