.notFound{
    width: 100vh;
    height: 100%;
    background-color: pink;
    margin: auto;

    >div{
        background-color: white;
        width: 100%;
        height: 80vw;
        text-align: center;
        justify-content: center;
        
        h1 {
            font-size: 36px;
            margin-bottom: 20px;
        }

        p {
            font-size: 18px;
            margin-bottom: 30px;
        }

        a {
            color: #007bff;
            text-decoration: none;
        }
    }
}