.founder{
    width: 100%;
    height: 100%;
    background-color: blueviolet;
    >div {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;
        width: 70%;
        text-align: center;
        margin: auto;
         
        >img {
            border-radius: 100%;
        }
        >h3{
            font-size: 1rem;
            padding: 2rem 0 1rem 0;
        }
        >p{
            letter-spacing: 2px;
            font-size: 0.8rem;
            font-family: cursive;
            line-height: 250%;
        }
    }
}

#menu{
width: 100%;
min-height: 100vh;
background-color: wheat;
padding: 4rem;
  

>h1{
    font-size: 3rem;
    text-align: center;
    padding: 2rem;

}
>div{
    display: flex;
    justify-content: space-evenly;
}
}
.menuCard{
    background-color: whitesmoke;
    width: 20%;
    border-radius: 20px;
    box-shadow: 0 0  10px black ;
    
    >div{
        border-radius: 20px 20px 0 0;
        background-color: red;
        display: flex;
        align-items: center;
        padding:2rem;
        color: white;
        height: 4rem;

    }
     >main{
        padding: 2rem;
        display:flex;
        flex-direction: column;
        align-items: center;

        >img{
            height: 15rem;
            width: 100%;
            object-fit: contain;

        }
        >h5{
            font-size: 1.8rem;
        }
        >p{
            text-transform: uppercase;
            letter-spacing: 2px;
            padding: 1rem 0;
        }

        >button{
            background-color: rgb(206, 29, 29);
            color: wheat;
            border: none;
            padding: 1rem 2rem;
            width: 100%;
            border-radius: 10px;
            cursor: pointer;
            &:hover{
                background-color: darkred;

            }
        }
    }
}

@media screen and (max-width: 600px,) {

    #menu>div{
        flex-direction: column;
        width:"100%";

    }
    .menuCard{
       width: auto;
        margin : 1rem 0;
        flex-direction: column;
    }
}
@media screen and (max-width: 900px,) {

    #menu>div{
        flex-direction: column;
        width:"100%";
  


    }
    .menuCard{
       width: auto;
        margin : 1rem 0;
        flex-direction: column;
    }
}