
nav{
    height:5rem;
    padding:2rem;
    display:flex;
    justify-content: space-between;
    align-items: center;
    box-shadow:2px 3px 10px rgb(130, 219, 247);
    
    background-color:rgb(72, 186, 221) ;
    
    

div{
    display:  fill;
    align-items: center;
   


svg{
    color: red;
    font-size: 3rem;
}
a{
    margin:0 2rem;
    color: rgb(3, 42, 112) ;
    transition: color 0.3s;

    &:hover{
        color:rgb(16, 85, 212);
    }
    svg{
        color: rgb(233, 10, 58);
        font-size: 1.5rem;

        &:hover{
            color:rgb(6, 48, 128);
       }
  }
}
}
}
@media screen and (max-width:600px){
    nav{
        padding:1rem;
        >div> a {
            margin: 0 0.5rem;
        }
    }
}