.Shipping{
    background-color: aqua;
    width: 100%;
    height: 100vh;
     >main{
        height: 100%;
        background-color: white;
        max-width: 900px;
        margin: auto;
        padding: 2rem;
        >h4{
            text-transform: uppercase;
            text-align: center;
            margin: 3rem;
            font: 100 3rem "Roboto";
        }
        >form{
            display: flex;
            flex-direction: column;
            align-items: center;

       
            >div{
                width: 70%;
                display: flex;
                align-items: center;
                justify-content: space-between;

                >label{
                    white-space: nowrap;
                }
                >input, >select{
                    width: 300px;
                    padding: 0.5rem 1rem;
                    outline: none;
                    border: 1px solid black;
                    border-radius: 5px;
                    margin: 1rem;
                    font-size: 1rem;
                }
            }
            >button{
                background-color: brown;
                color: aqua;
                border: none;
                padding: 0.5rem 1rem;
                border-radius: 5px;
                margin: 1rem;
                text-align: center;
                cursor: pointer;
                font-size: 1.1rem;
            }
        }
     }
    }
    @media screen and (max-width: 632px) {
        .Shipping >main >form
        { >div {
            width: 100%;
        }
        > button {
            width: 100%;
        }
    }
    }
    @media screen and (max-width: 400px) {
        .Shipping >main >form
        { >div {
            width: 100%;
            >input , >select{
                width: 100%;
              
            }
        }
    
        
        > button {
            width: 100%;
        }
    }
    }
    .confirmOrder{
