footer{
    min-height: 300px;
    background-color: rgb(119, 41, 165);
    padding: 4rem;
    display:grid;
    grid-template-columns: 2fr 1fr;
    color: #ffffff;
padding-left: 1rem;   

    h2{
        color: aqua;
        font-family: fantasy;
        font-weight: 400;
        font-size:2rem;
      
    }
p,strong{
    margin: 1rem;

}
aside{
    height:100%;
    display: flex;
    flex-direction:column ;
    align-items: center;
    padding-right: 0.5rem;
    padding-inline-start: 0%;
}
h4{
    font-size:1.1rem;
}
a{
    margin:0.5rem;
    font-size: 2rem;
    color:white;
    &:hover{
        color: red;
    }


}

}
@media screen and (max-width: 500px,) {
    footer aside {
        height: fit-content;
        width: fit-content;
        align-items: flex-start;
    }
}
@media screen and (max-width: 363px) {
    footer aside {
        height: fit-content;
        width: fit-content;
        align-items: flex-start;
        margin: -2%;
    }
    div{margin: -3%;

        h2{

        }
    }
}

