.login{
    height: 60vh;
    width: 1005;
    background-color: pink;
    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;
    .google{
        background-color: white;
        color: rgb(110, 119, 29);
        border: none;
        border-radius: 10px;
        padding: 1rem 2rem;
        font-size: 1.1rem;
        display: flex;
       margin: 0.5rem;
        align-items: center;
        cursor: pointer;
        &:hover{
            color: brown;
        }
    }
    .facebook{
    background-color: rgb(81, 81, 238);
    color: rgb(14, 15, 3);
    border: none;
    border-radius: 10px;
    padding: 1rem 2rem;
    font-size: 1.1rem;
    display: flex;
   margin: 0.5rem;
    align-items: center;
    cursor: pointer;
    &:hover{
        color: brown;
    }
    }
}
