.dashboard{
    background-color: white;
    height: 100vh;
    width: 100%;
    >main{
        background-color: pink;
        height: 100%;
        max-width: 900px;
        margin: auto;
        overflow-x: auto;
        >article{
            background-color: black;
            padding: 2rem;
            justify-content: space-evenly;
            align-items: center;
            display: flex;
            >div{
                background-color: white;
                border-radius: 100%;
                text-align: center;
                height: 150px;
                width: 150px;
                padding: 2rem;
                >h3{
                    font: 800 2rem "Roboto";
                }
                >p{
                    font: 800 2rem "Roboto";
                }
            }
    
        }

            >section{
                display: flex;
                justify-content: space-between;
                padding: 6rem;
                >div{
                    display: flex;
                    flex-direction: column;
                    width: 50%;
                    justify-content: center;
                    align-items: center;
                    >a{
                        white-space: nowrap;
                        background-color: rgb(206, 165, 165);
                        padding: 1rem;
                        width: 70%;
                        text-align: center;
                        border-radius: 5px;
                        margin: 1rem;

                    }
                }
                >aside{
                    width:80%;
                }
            }

    }


}
@media screen and (max-width:860px) {
  .dashboard{
    height: 100vmin;
    >main >section{
        padding: 1rem;
    }

  }
    
}
@media screen and (max-width:560px) {
    .dashboard{
      height: unset;
      min-height: 100%;
      >main >section{
          padding:1rem;
    
      }
      article{
      
       width: 60vh;
        display: flex;
        flex-direction: row;
        >div{
            margin: 0.1rem;
            >a{
                border-radius: 100%;
            }
        }
      }
  
    }
      
  }