// cart
.cart{
    background: lightpink;
    width: 100%;
    height: 100vh;

    >main{
        height: 100%;
        background-color: #14d5b3;
        max-width: 900px;
        margin: auto;
        padding: 2rem;
        >article{
            padding: 2rem;
           >div{
            padding: 0.7rem;
            justify-content: space-between;
            margin: 1 rem 0;
            display: flex;
           }
           >a{
            background-color: red;
            color: white;
            padding: 1rem 0.5rem;
            border-radius:5px ;
            float:right ;
            margin-top: 1rem;
           }
        }
    }
}



  .myname{
    background-color: rgb(245,246,246);
    margin: 1rem;
    display: flex;
    align-items:center ;
    justify-content: space-between;
    padding: 1rem;
    border-radius: 5px;
    
    >div{
        display: flex;
        align-items: center;

        >h4{
            font-weight: 200;
            letter-spacing: 2px;
        }
        >img{height:50px;}
        >input{
            text-align: center;
            width: 2rem;
            height: 2rem;
            padding: 0.5rem;
            border: none;
            margin: 0.25rem;
            outline: none;
            border-radius: 5px;
            color :red;
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button{
                -webkit-appearance: none;
                margin: 0;
            }
       
    }
    >button{
        cursor: pointer;
        border: none;
        padding: 0.5rem ;
        background-color: black;
        color: white;
        border-radius: 5px;
    }
  }
}
