.profile{
    background-color: aqua;
    height:70%;
    width: 100%;
    

    >main{
        background-color: aquamarine;
        width:80%;
        height: 70%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-around;
        margin: auto;
        padding: 9rem;
        >img{
            height:15rem;
            width: 15rem;
            align-items: center;
            display: flex;
            border-radius: 50%;
            margin: 1rem;
            }
            >h5
                {
                    padding: 0.5rem 6rem;
                    font-weight: 900;
                    font-size: 51px;
                }
            >div{
                width:auto;
                height: 70%;
               color: black;
                margin: 0.5rem;
                border: 1px solid;
                border-radius: 3px 3px 3px 3px;
                padding: 0.5rem 3rem;
                background: #e4dcde;
                font-size: 21px;
                display: flex;
                justify-content: space-around;
                
            
            }
            >button{
           
              
                color: black;
                 margin: 0.5rem;
                 border: 1px solid;
                cursor: pointer;
                 padding: 0.5rem 4rem;
                 background: #f13866;
                 font-size: 21px;
                 &:hover{
                    background-color: #e4dcde;
                 }
             
             }

            }
    }
