$color1: #e07d5f; // Replace with your first color
$color2: #bdce24; // Replace with your second color
$color3: #3d9fb1; // Replace with your third color
$color4: #292323; 


.contact{
    height: 100vh;
    width: 100%;
    background: linear-gradient(60deg, $color1, $color2, );
    display: flex;
    align-items: center;
    justify-content: center;

    >form{
        width: 40%;
        height: 80%;
        padding: 10rem 10rem;
        background: $color3;
        align-items: center;
          display: flex;
       flex-direction: column;
      
    >h4{
        font-size: 26px;
        font-weight: 900;
        align-items: center;
    }   
    >input{
        margin: 5px 5px;
        justify-content: flex-start;
        display: flex;
        height: 10%;
        width: 500px;
        outline: none;
        padding: 1rem;
    }
    >textarea{
        margin: 0.5rem;
        height: 40%;
        width: 500px;
        outline: none;
        padding: 1rem;

    }
    >button{
        margin: 5px 5px;
        align-items: center;
        background-color: $color4;
        height: 10%;
        width: 500px;
        padding: 0.5rem;
        cursor: pointer;
        &:hover{
            background-color: greenyellow;
        }
    }
    
    }
}
.formBorder{
    width: 200px;
    height: 80%;
    background-color:$color3;
    border-radius: 0 200px 200px 0;
    position: relative;
   
    >div{
        position: absolute;
        height: 200px;
        width: 200px;
        top: 40%;
        transform: translate(50%,-50%);
        filter: drop-shadow(0 0 10px rgb(0, 0, 0 ,0.566));


        >img{
            width: 100%;
        }

    }
}
iframe#webpack-dev-server-client-overlay {
    display: none;
}

@media screen and (max-width: 500px) {
   
    .contact{
        width: 100%;

        >form{
            width: 60%;
            height: 60%;
            align-items: center;
          }
        
    }
    .formBorder{
        display: none;
    }

}
@media screen and (max-width: 900px) {

    .contact{
        width: 100%;
        >form{
            width: 50%;
            >h4{
                font-size: 16px;
                width: 100px;
            }
            >input ,button{
                width: 300px;
                height: 2rem;
            }
            >textarea{
                height: 100px;
                width: 0300px;
                margin: 0.5rem;
            }
        
          }
    
    }
    .formBorder{
        display: none;
    }

    
}
