.home{
    height: 100vh;
    overflow-x:hidden;

    div{
        padding-left: 3rem;
        padding-top: 15rem ;
        padding-bottom: 2rem;
      

        color: rgb(38, 8, 206);
        >h1{
            font-size: 4rem;
        }
        >p{
            font-size: 1.2rem;
        }
    }
    >a{
        margin: 12rem 5rem;
        
        background-color: rgb(50, 107, 212);
        width: 10rem;
        color: white;
        text-align: center;
        border: none;
        font-size: 1rem;
        border-radius: 10px;
        padding: 1rem;
   
    
        &:hover{background-color: rgb(7, 72, 192);
        }
    }

  &::before {
    content:"";
    width: 100%;
    height: 100%;
    background-image: url(../images/logo.png);
    background-repeat: no-repeat;
    background-size:cover;
    
    position: absolute;
    left: 0;
    z-index: -1;
}
@media screen and (max-width:600px){
.home{
    >div{
        padding-left: 5rem;
        padding-top: 10rem ;
        padding-bottom: 2rem;
        width: 100px;

    }
    >a{
        margin: 1000;
        padding-left: 4rem;
    }
}
}
}